@import "../../../scss/variables";

/* Center ratings in container */
/* Display star ratings */
/* Boxes */
.box-example-1to10 {
	.br-wrapper {
		width: 210px;
		position: absolute;
		margin: 0px 0 0 -105px;
		left: 50%;
	}
}
.box-example-movie {
	.br-wrapper {
		width: 250px;
		position: absolute;
		margin: 0px 0 0 -125px;
		left: 50%;
	}
}
.box-example-square {
	.br-wrapper {
		width: 190px;
		position: absolute;
		margin: 0px 0 0 -95px;
		left: 50%;
	}
}
.box-example-pill {
	.br-wrapper {
		width: 232px;
		position: absolute;
		margin: 0px 0 0 -116px;
		left: 50%;
	}
}
.box-example-reversed {
	.br-wrapper {
		padding-top: 1.3em;
		width: 356px;
		position: absolute;
		margin: 0px 0 0 -178px;
		left: 50%;
	}
}
.box-example-horizontal {
	.br-wrapper {
		width: 120px;
		position: absolute;
		margin: 0px 0 0 -60px;
		left: 50%;
	}
}
.star-ratings {
	h1 {
		font-size: 1.5em;
		line-height: 2;
		margin-top: 3em;
		color: #757575;
		text-align: center;
	}
	p {
		margin-bottom: 3em;
		line-height: 1.2;
		text-align: center;
	}
	.stars {
		text-align: center;
		margin: auto;
		.title {
			font-size: 14px;
			color: #cccccc;
			line-height: 3;
		}
		select {
			font-size: 16px;
		}
	}
}
.start-ratings-main {
	margin-bottom: 3em;
}
.box {
	width: 100%;
	float: left;
	.box-header {
		text-align: center;
		font-weight: 400;
		padding: .5em 0;
	}
	.box-body {
		padding-top: 2em;
		height: 85px;
		position: relative;
	}
	select {
		width: 120px;
		margin: 10px auto 0 auto;
		display: block;
		font-size: 16px;
	}
}
.box-large {
	.box-body {
		padding-top: 2em;
		height: 120px;
	}
}
.box-orange {
	.box-header {
		background-color: #edb867;
		color: white;
	}
	.box-body {
		background-color: white;
		border-top: 0;
	}
}
.box-green {
	.box-header {
		background-color: #50e3c2;
		color: white;
	}
	.box-body {
		background-color: white;
		border: 2px solid #92eed9;
		border-top: 0;
	}
}
.box-blue {
	.box-header {
		background-color: #4278f5;
		color: white;
	}
	.box-body {
		background-color: white;
		border: 2px solid #8bacf9;
		border-top: 0;
	}
}
@media print {
	.star-ratings {
		h1 {
			color: black;
		}
		.stars {
			.title {
				color: black;
			}
		}
	}
	.box-orange {
		.box-header {
			background-color: transparent;
			color: black;
		}
		.box-body {
			background-color: transparent;
			border: none;
		}
	}
	.box-green {
		.box-header {
			background-color: transparent;
			color: black;
		}
		.box-body {
			background-color: transparent;
			border: none;
		}
	}
	.box-blue {
		.box-header {
			background-color: transparent;
			color: black;
		}
		.box-body {
			background-color: transparent;
			border: none;
		}
	}
}
